// Calculate the luminance for a color.
// See https://www.w3.org/TR/WCAG20-TECHS/G17.html#G17-tests
@function vx-luminance($color) {
  $red: nth($vx-linear-channel-values, red($color) + 1);
  $green: nth($vx-linear-channel-values, green($color) + 1);
  $blue: nth($vx-linear-channel-values, blue($color) + 1);

  @return .2126 * $red + .7152 * $green + .0722 * $blue;
}

// Calculate the contrast ratio between two colors.
// See https://www.w3.org/TR/WCAG20-TECHS/G17.html#G17-tests
@function vx-contrast($back, $front) {
  $backLum: vx-luminance($back) + .05;
  $foreLum: vx-luminance($front) + .05;

  @return max($backLum, $foreLum) / min($backLum, $foreLum);
}

@function vx-darker-color($color1, $color2) {
  @if (vx-luminance($color1) > vx-luminance($color2)) {
    @return $color2;
  } @else {
    @return $color1;
  }
}

@function vx-lighter-color($color1, $color2) {
  @if (vx-luminance($color1) > vx-luminance($color2)) {
    @return $color1;
  } @else {
    @return $color2;
  }
}


$defaultColors: (
  primary: #3fa0bd,
  background: #fcfcfc,
  foreground: black,
  error: #bf0000,
  success: #51a351,
  warn: #f89406,
  accent: #FFCA4F
);

$_vxNsPreparedMaps: ();
@function vx-prepare-colors($colors: ()) {
  $hash: inspect($colors);

  @if map_has_key($_vxNsPreparedMaps, $hash) {
    @return map_get($_vxNsPreparedMaps, $hash);
  }

  $colors: map-merge($defaultColors, $colors);

  $primary: map_get($colors, primary);
  $background: map_get($colors, background);
  $foreground: map_get($colors, foreground);
  $error: map_get($colors, error);
  $success: map_get($colors, success);
  $warn: map_get($colors, warn);
  $accent: map_get($colors, accent);
  $darkerBgFg: vx-darker-color($background, $foreground);
  $lighterBgFg: vx-lighter-color($background, $foreground);
  $borderLight: mix($darkerBgFg, $lighterBgFg, 40%);
  $borderDark: mix($darkerBgFg, $lighterBgFg, 50%);

  $result: map-merge($colors, (
    darkerBgFg: $darkerBgFg,
    lighterBgFg: $lighterBgFg,
    borderLight: $borderLight,
    borderDark: $borderDark
  ));

  $newHash: inspect($result);

  $_vxNsPreparedMaps: map_merge($_vxNsPreparedMaps, ($hash: $result, $newHash: $result)) !global;
  @return $result;
}

// Use this mixin to apply colored themes to the dropdown
@mixin vx-form-field-theme($colors) {
  $colors: vx-prepare-colors($colors);
  $background: map_get($colors, background);
  $foreground: map_get($colors, foreground);
  $primary: map_get($colors, primary);
  $error: map_get($colors, error);
  $accent: map_get($colors, accent);
  $border: map_get($colors, borderLight);

  $darkerBgFg: map-get($colors, darkerBgFg);
  $lighterBgFg: map-get($colors, lighterBgFg);

  $placeholder: mix($darkerBgFg, $lighterBgFg, 30%);
  $darkerPlaceholder: mix($darkerBgFg, $lighterBgFg, 50%);
  $labelColor: map_get($colors, borderLight);

  // Initial Colors
  .vx-form-field-container {
    border: 1px solid $border;
    background: $background;
  }

  .vx-form-field-label, .vx-form-field-required-label {
    color: $labelColor;
  }

  .vx-form-field-input {
    color: $foreground;

    &::placeholder {
      color: $placeholder;
    }
  }

  // Focused Colors
  .vx-form-field.vx-focused {
    .vx-form-field-container {
      border-color: $primary;
    }

    .vx-form-field-label {
      color: $primary;
    }
    .vx-form-field-required-label {
      color: $accent;
    }

    .vx-form-field-input::placeholder {
      color: $darkerPlaceholder;
    }
  }

  // Error Colors
  .vx-form-field.vx-error {
    .vx-form-field-container {
      border-color: $error;
    }

    .vx-form-field-label {
      color: $error;
    }

    .vx-form-field-required-label {
      color: mix($accent, $error);
    }

    .vx-form-field-input::placeholder {
      color: mix($error, $placeholder, 20%);
    }
  }

  // Error and Focused Colors
  .vx-form-field.vx-error.vx-focused {
    .vx-form-field-input::placeholder {
      color: mix($error, $darkerPlaceholder, 30%);
    }
  }

}

@mixin vx-radio-theme($colors) {
  $colors: vx-prepare-colors($colors);
  $primary: map_get($colors, primary);
  $foreground: map_get($colors, foreground);
  $background: map_get($colors, background);
  $accent: map_get($colors, accent);
  $error: map_get($colors, error);
  $border: map_get($colors, borderLight);

  .vx-radio-button-fill {
    background: $primary;
  }

  .vx-radio-button-outline {
    border: 2px solid $primary;
  }

  .vx-radio-button {
    &:hover .vx-radio-button-outline {
      background: mix($foreground, $background, 3%)
    }
    &:active .vx-radio-button-outline {
      background: mix($foreground, $background, 10%)
    }

    &.vx-focused .vx-radio-button-outline {
      box-shadow: 0 0 2px 1px transparentize($primary, .5);
    }
  }

  // Error state
  .vx-radio-button.vx-error {
    .vx-radio-button-fill {
      background: $error;
    }

    .vx-radio-button-outline {
      border-color: $error;
    }

    &.vx-focused .vx-radio-button-outline {
      box-shadow: 0 0 2px 1px transparentize($error, .5);
    }
  }
}

// Use this mixin to apply colored themes to the dropdown
@mixin vx-menu-theme($colors) {
  $colors: vx-prepare-colors($colors);
  $background: map_get($colors, background);
  $foreground: map_get($colors, foreground);
  $primary: map_get($colors, primary);
  $border: map_get($colors, borderLight);
  $focused: mix($foreground, $background, 6%);

  .vx-menu {
    color: $foreground;
    border: 1px solid $border;
    box-shadow: 0 2px 4px 0 rgba($foreground, .2);
    background: $background;
  }

  .vx-item {
    background: $background;

    &:not(:first-child) {
      border-top: 1px solid $border;
    }

    &:hover {
      background: $focused;
    }

    &:focus, &.vx-focused {
      outline: none;
      background: $focused;
      font-weight: bold;
    }

    &:active, &.vx-active {
      background: $primary;
      color: $background;
    }
  }

}

