  $primaryColorHover: mix($darkerBgFg, $primary, 12%);
  $primaryColorActive: mix($darkerBgFg, $primary, 16%);

  $accentColor: $accent;
  $accentColorFocus: mix($darkerBgFg, $accent, 8%);
  $accentColorHover: mix($darkerBgFg, $accent, 12%);
  $accentColorActive: mix($darkerBgFg, $accent, 16%);

