      color: $darkerBgFg;


      &:focus {
       background: $lightColorFocus;
      }
      &:hover {
        background: $lightColorHover;
      }
      &:active {
        background: $lightColorActive;
      }
    }

    &.vx-button-dark {
      background: $darkColor;
      color: $lighterBgFg;

      &:focus {
       background: $darkColorFocus;
      }
      &:hover {
        background: $darkColorHover;
      }
      &:active {
        background: $darkColorActive;
      }
    }

    &.vx-button-primary {
      background: $primaryColor;
      color: $lighterBgFg;

      &:focus {
       background: $primaryColorFocus;
      }
      &:hover {
        background: $primaryColorHover;
      }
      &:active {
        background: $primaryColorActive;
      }
    }

    &.vx-button-accent {
      background: $accentColor;
      color: $lighterBgFg;

      &:focus {
       background: $accentColorFocus;
      }
      &:hover {
        background: $accentColorHover;
      }
      &:active {
        background: $accentColorActive;
      }
