}

@mixin vx-spinner-theme($colors) {
  $colors: vx-prepare-colors($colors);
  $primary: map_get($colors, primary);
  $primary: map_get($colors, primary);
  $error: map_get($colors, error);
  $accent: map_get($colors, accent);
  $success: map_get($colors, success);
  $warn: map_get($colors, warn);

  $darkerBgFg: map-get($colors, darkerBgFg);
  $lighterBgFg: map-get($colors, lighterBgFg);

  .vx-spinner-primary .vx-spinner-circle:before {
      background-color: $primary;
  }

  .vx-spinner-light .vx-spinner-circle:before {
    background-color: $lighterBgFg;
  }

  .vx-spinner-dark .vx-spinner-circle:before {
    background-color: $darkerBgFg;
  }

  .vx-spinner-error .vx-spinner-circle:before {
    background-color: $error;
  }

  .vx-spinner-success .vx-spinner-circle:before {
    background-color: $success;
  }

  .vx-spinner-warn .vx-spinner-circle:before {
    background-color: $warn;
  }

  .vx-spinner-accent .vx-spinner-circle:before {
    background-color: $accent;
  }
}


@mixin vx-all-components($colors: ()) {
  $colors: vx-prepare-colors($colors);

  @include vx-form-field-theme($colors);
  @include vx-radio-theme($colors);
  @include vx-menu-theme($colors);
  @include vx-autocomplete-theme($colors);
  @include vx-button-theme($colors);
  @include vx-tabs-theme($colors);
  @include vx-checkbox-theme($colors);
  @include vx-stepper-theme($colors);
  @include vx-dialog-theme($colors);
  @include vx-spinner-theme($colors);
}

.vx-visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.vx-overlay-modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.vx-overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-tap-highlight-color: transparent;
}

.vx-overlay-container {
  position: fixed;
  overflow: visible;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
